.section--jobs {
    text-align: center;
    background: #b07cf7;
    color: #fff;
    a,
    .button,
    .job {
        transition: all .3s ease-in;
        text-decoration: none;
        color: #fff;
    }


    h1 {
        margin: 75px auto auto;
    }

    .jobs-title{
        margin: 0;

        p {
            font-size: 1.7rem;
            text-align: center;
            margin: 2.5rem auto 4.5rem;
            width: calc(100%*65/120);
            /* border: 1px solid white; */
            line-height: 2.5rem;
            margin-bottom: 0px;
        }
    }

    & .jobs-list {
        // outline: 1px  solid green;
        display: flex;
        font-size: 0;
        margin: 3rem -5rem 0 -5rem;
        justify-content: space-evenly;
        text-align: left;
        flex-wrap: wrap;
    }

    & .job {
        // outline:  1px solid red;
        /* border: 1px solid white; */
        user-select: none;
        // cursor: alias;
        width: 40%;
        overflow: hidden;
        background: rgb(255, 242, 67);
        position: relative;
        height: 31rem;
        box-shadow: 0 .3rem 0.5rem rgba( 0, 0, 0, 0.4);

        @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
            & {
               margin-left: 40px;
            }
        }

        @supports (-ms-ime-align: auto) {
            & {
                margin-left: 40px;
            }
        }


        &__link{
            position: absolute;
            left: 50%;
            bottom: 5px;
            transform: translate(-50%, 0) scale(.9);
            border-radius: .5rem;
            overflow: hidden;
            width: auto;
            background: rgba(0, 0, 0, 0.685);
            box-shadow: 0 0 2px 0 rgba(214, 214, 214, 0.685);
            padding: 5px;


            @include hover{
                transform: translate(-50%, 0) scale(1);
                box-shadow: 0 0 5px 3px rgb(255, 255, 255);
            }

            &--cpi{
                box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.685);
                transform: translate(-50%, 0) scale(.9);
                background: rgb(255, 255, 255);
                @include hover{
                    transform: translate(-50%, 0) scale(1);
                    box-shadow: 0 0 5px 3px rgba(0, 0, 0, 0.685);
                }

            }

            & img {
                display: inline-block;
                position: relative;
                object-fit: contain;
                height: 60px;
                width: auto;
                max-width: 200px;
            }

        }

        &__employer {
            text-decoration: underline;
            cursor: pointer;
    
            &:not(:empty):before {
                content: "At ";
                text-decoration: none;
            }
    
            &:not(:empty):after {
                content: " - ";
                text-decoration: none;
            }
    
            @include hover{
                color: rgb(148, 148, 255);
            }
        }
    }

    input:checked+.job .article,
    input:checked+.job .article,
    input:checked+.job .article {
        top: -31rem;
    }

    img {
        position: absolute;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: 0 100%;
    }


    .article {
        transition: all .5s ease-in-out;
        /* border: 1px solid white; */
        height: 62rem;
        padding: 0 1.5rem;
        position: relative;
        top: 0rem;
        background: linear-gradient(rgba( 0, 0, 0, 0), rgba( 0, 0, 0, 0), rgba( 0, 0, 0, 0.9), rgba( 0, 0, 0, 0.3), rgba( 0, 0, 0, 0));
    }

    .article__date {
        text-align: center;
        width: auto;
        padding: 0 10px;
        height: 6rem;
        border-radius: 0.5rem;
        vertical-align: middle;
        background: rgba(0, 0, 0, 0.3);
        position: absolute;
        left: 3rem;
        top: 3rem;
    }

    .article__date p {
        font-size: 2.2rem;
        margin: auto auto 0 auto;
        font-weight: 700;
        position: relative;
        top: 50%;
        transform: translate(0, -50%);
    }

    .article h2 {
        /* border: 1px solid white; */
        font-size: 2rem;
        text-align: left;
        position: absolute;
        bottom: 36rem;
    }

    .article__info {
        /* border: 1px solid white; */
        font-size: 1.3rem;
        position: absolute;
        bottom: 33rem;
        max-width: calc(100% - 3rem);
    }


    .article article p {
        font-size: 1.7rem;
        text-align: left;
        line-height: 2.5rem;
        position: absolute;
        top: 33rem;
        max-height: 20rem;
        overflow: hidden;
        margin: 0;
        width: calc(100% - 3rem);
        text-shadow: 2px 2px 2px rgba( 0, 0, 0, 0.99), 0px 0px 10px rgba( 0, 0, 0, 0.99);
    }

    .button {
        display: inline-block;
        margin: 4.5rem 1.5rem 7.5rem;
        text-transform: uppercase;
        font-size: 2rem;
        letter-spacing: 1px;
        padding: 2rem 1rem 2rem 1rem;
        background: #f72b3b;
        box-shadow: 0 .3rem 0.5rem rgba( 0, 0, 0, 0.4);
        @include hover {
            color: #f72b3b;
            background: #fff;
        }
    }
}

@media screen and (max-width:768px) {
    
    .section--jobs {
        .job {
            width: 400px;
            margin: auto;
        }
        .job+input+.job {
            margin-top: 45px;
        }

        .jobs-title{
            margin: 3rem -50px 0 -50px;
            p {
                width: 98%;
            }
        } 
    }

}

@media screen and (max-width:576px) {
    .section--blog {
        .section__title {
            margin-top: 1.5rem;
        }
    }

}
